import org.w3c.dom.HTMLCanvasElement
import vision.gears.webglmath.UniformProvider
import vision.gears.webglmath.Vec2
import vision.gears.webglmath.Vec3
import kotlin.js.Date
import kotlin.random.Random
import kotlin.math.cos
import kotlin.math.exp
import kotlin.math.sin
import org.khronos.webgl.WebGLRenderingContext as GL

// Az alábbi öt feladatrészt választottam
// 1. Avatar: Legyen olyan objektum, amit a játékos irányít, gombok lenyomva tartásával erőket és forgatónyomatékokat beállítva. Emellett lehet olyan mozgás (pl. ugrás), ami nem így működik, hanem pillantszerű erőlökést ad.
// 2. Kamera (Avatar kell): A nézet kövesse az avatart.
// 3. Lövedék: Gombnyomással vagy egérkattintással lehessen új, mozgó objektumot létrehozni. Legyen egy lehűlési időszak, aminek el kell telnie két lövedék kilövése között.
// 4. Közeg: Minden objektum folyamatosan veszítsen a lendületéből és perdületéből.
// 5. Ütközés: Az objektumokat körlapoknak tekintve legyen ütközésdetektálás, és valamiféle hatása (pl. objektum eltűnik, objektum színt vált, megjelentett szám csökken) legyen az ütközésnek.

class Scene(
        val gl: WebGL2RenderingContext) : UniformProvider("scene") {

    val vsTrafo = Shader(gl, GL.VERTEX_SHADER, "shaders/trafo-vs.glsl")
    val fsTextured = Shader(gl, GL.FRAGMENT_SHADER, "shaders/textured-fs.glsl")

    val texturedProgram = Program(gl, vsTrafo, fsTextured)
    val gameObjects = ArrayList<GameObject>()
    val Camera = OrthoCamera(*Program.all)
    fun addToGameObjects(gameObject: GameObject) {
        gameObjects.add(gameObject);
    }

    // Bocsánat, tudom hogy mennyire csúnya, de sürget az idő

    var avatarHealth = 100
    var NorbiHealth = 100
    var avatarPosition = Vec3()
    var avatarScale = Vec3()

    val quadGeometry = TexturedQuadGeometry(gl)
    val arnoldMaterial = Material(texturedProgram)
    val norbiMaterial = Material(texturedProgram)
    val proteinMaterial = Material(texturedProgram)
    val strongMaterial = Material(texturedProgram)
    val weakMaterial = Material(texturedProgram)
    var timeAtLastShoot = Date().getTime().toFloat()



    var avatar = object : GameObject(Mesh(arnoldMaterial, quadGeometry), Vec3(-3f, -3f), 0f, Vec3(1f, 1f), 5f, Vec3(0f, 0f), 5f, 5f, 0f) {

        val c2 = 50f // second air-resistance coefficient

        override fun move(dt: Float, t: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {

            val F = Vec3(0f, 0f) // Force
            val Fsize = 50f;

            var tau = 0f // Torque, always tangential
            val tausize = 15f;

            if (keysPressed.contains("A")) {
                tau += 1f
            }
            if (keysPressed.contains("D")) {
                tau -= 1f
            }
            if (keysPressed.contains("W")) {
                F += Vec3(1f, 0f) * sin(-roll)
                F += Vec3(0f, 1f) * cos(roll)
            }
            if (keysPressed.contains("S")) {
                F += Vec3(1f, 0f) * sin(roll)
                F -= Vec3(0f, 1f) * cos(roll)
            }
            if (keysPressed.contains("E")) {
                F += Vec3(1f, 0f) * cos(roll)
                F += Vec3(0f, 1f) * sin(roll)
            }
            if (keysPressed.contains("Q")) {
                F -= Vec3(1f, 0f) * cos(roll)
                F += Vec3(0f, 1f) * sin(-roll)
            }
            if (keysPressed.contains("P") && Date().getTime().toFloat() - timeAtLastShoot > 400f) {
                Shoot()
                timeAtLastShoot = Date().getTime().toFloat()
            }
            tau *= tausize

            var beta = tau / theta // angular acceleration
            omega += beta * dt
            roll += omega * dt
            omega *= exp(-dt * c / theta)

            F *= Fsize

            var a = F / m // acceleration
            v += a * dt
            position += v * dt

            v *= exp(-dt * c / m)


//            Próbálkozás a 'Terelő' megvalósítására...

//            while (roll >= 2f * kotlin.math.PI.toFloat())
//                roll -= 2f * kotlin.math.PI.toFloat()
//            while (roll <= -2f * kotlin.math.PI.toFloat())
//                roll += 2f * kotlin.math.PI.toFloat()
//
//            console.log("original: " + v.x)
//            console.log("original: " + v.y)
//            val v1 = v*sin(roll)
//            val v2 = v*cos(roll)
//
//            v1 *= exp(-dt * c / m)
//            v2 *= exp(-dt * c2 / m)
//            var len = abs(v1.length() + v2.length())
//            if (0f <= roll && roll <= kotlin.math.PI.toFloat() / 2f)
//                v.set(len * cos(roll), len * sin(roll))
//            if (kotlin.math.PI.toFloat() / 2f <= roll && roll <= kotlin.math.PI.toFloat())
//                v.set(len * -cos(roll), len * sin(roll))
//            if (kotlin.math.PI.toFloat() <= roll && roll <= 3f * kotlin.math.PI.toFloat() / 2f)
//                v.set(len * -cos(roll), len * -sin(roll))
//            if (3f * kotlin.math.PI.toFloat() / 2f <= roll && roll <= 2f * kotlin.math.PI.toFloat())
//                v.set(len * cos(roll), len * -sin(roll))


            // Ezt tényleg csak az idő miatt csináltam :(
            avatarPosition = position
            avatarScale = scale
            return avatarHealth > 0
        }

        fun Shoot() {

            var projectile = object : GameObject(Mesh(proteinMaterial, quadGeometry), Vec3(position.x, position.y), roll, Vec3(0.15f, 0.15f), 20f, Vec3(0f, 0f), 5f, 5f, 0f) {
                val velocity = Vec3(0f, 0f)
                val F = Vec3(50 * cos(roll), 50 * sin(roll)) // Force

                override fun move(dt: Float, t: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {
                    var distanceVector = NorbiObject.position - this.position

                    var a = F / m // acceleration

                    F *= 0.985f

                    v += a * dt
                    position += v * dt
                    v *= exp(-dt * c / m)

                    position += velocity * dt
                    if (distanceVector.length() < (NorbiObject.scale.x + this.scale.x)) {
                        --NorbiHealth
                        return false
                    }
                    if (position.x < Camera.position.x - Camera.windowSize.x || position.x > Camera.position.x + Camera.windowSize.x)
                        return false
                    if (position.y < Camera.position.y - Camera.windowSize.y || position.y > Camera.position.y + Camera.windowSize.y)
                        return false
                    return true
                }
            }
            addToGameObjects(projectile);

        }
    }

    var NorbiObject = object : GameObject(Mesh(norbiMaterial, quadGeometry), Vec3(-3f, -3f), 0f, Vec3(1f, 1f), 5f, Vec3(0f, 0f), 5f, 5f, 0f) {
        val velocityX = Vec3(0.01f, 0.0f)
        var dir = 1.0f
        var timeAtLastShoot = Date().getTime().toFloat()

        override fun move(dt: Float, t: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {
            if (position.x > 1.0f)
                dir = -1.0f
            if (position.x < -1.0f)
                dir = 1.0f
            if (Date().getTime().toFloat() - timeAtLastShoot > 500f && Random.nextInt() % 3 == 0) {
                Shoot()
                timeAtLastShoot = Date().getTime().toFloat()
            }
            position += velocityX * dir
            return NorbiHealth > 0
        }



        fun Shoot() {
            val velocityX = Vec3(5f, 0f)
            val velocityY = Vec3(0f, 5f)

            var projectile = object : GameObject(Mesh(proteinMaterial, quadGeometry), Vec3(position.x, position.y), roll, Vec3(0.15f, 0.15f), 20f, Vec3(0f, 0f), 5f, 5f, 0f) {
                val velocity = Vec3(0f, 0f)
                val F = Vec3(50 * (avatarPosition.x - position.x), 50 * (avatarPosition.y - position.y)) // Force

                override fun move(dt: Float, t: Float, keysPressed: Set<String>, gameObjects: List<GameObject>): Boolean {
                    var distanceVector = avatarPosition - this.position

                    var a = F / m // acceleration

                    F *= 0.985f

                    v += a * dt
                    position += v * dt
                    v *= exp(-dt * c / m)

                    position += velocity * dt
                    if (distanceVector.length() < (avatarScale.x + this.scale.x)) {
                        --avatarHealth
                        return false
                    }
                    if (position.x < Camera.position.x - Camera.windowSize.x || position.x > Camera.position.x + Camera.windowSize.x)
                        return false
                    if (position.y < Camera.position.y - Camera.windowSize.y || position.y > Camera.position.y + Camera.windowSize.y)
                        return false
                    return true
                }
            }
            addToGameObjects(projectile);

        }

    }

    init {
        gl.enable(GL.BLEND)
        gl.blendFunc(GL.SRC_ALPHA, GL.ONE_MINUS_SRC_ALPHA)
        addComponentsAndGatherUniforms(*Program.all)

        arnoldMaterial["colorTexture"]?.set(Texture2D(gl, "media/arnold.png"))
        norbiMaterial["colorTexture"]?.set(Texture2D(gl, "media/norbi.png"))
        proteinMaterial["colorTexture"]?.set(Texture2D(gl, "media/protein.png"))
        strongMaterial["colorTexture"]?.set(Texture2D(gl, "media/strong.png"))
        weakMaterial["colorTexture"]?.set(Texture2D(gl, "media/weak.png"))



        NorbiObject.scale.set(Vec3(0.3f, 0.3f, 1.0f))
        NorbiObject.position.set(Vec3(0.6f, 0.6f))
        gameObjects.add(NorbiObject)

        avatar.scale.set(Vec3(0.3f, 0.3f, 1.0f))
        gameObjects.add(avatar)

    }

    fun resize(gl: WebGL2RenderingContext, canvas: HTMLCanvasElement) {
        gl.viewport(0, 0, canvas.width, canvas.height)
        Camera.setAspectRatio(canvas.width.toFloat() / canvas.height.toFloat())
    }

    val timeAtFirstFrame = Date().getTime().toFloat()
    var timeAtLastFrame = timeAtFirstFrame
    var follow = false
    var timeAtLastSpace = Date().getTime().toFloat()

    @Suppress("UNUSED_PARAMETER")
    fun update(gl: WebGL2RenderingContext, keysPressed: Set<String>) {

        // set time
        val dt = (Date().getTime().toFloat() - timeAtLastFrame.toFloat()) / 1000.0f
        val t = (Date().getTime().toFloat() - timeAtFirstFrame.toFloat()) / 1000.0f
        timeAtLastFrame = Date().getTime().toFloat()


        // clear the screen
        gl.clearColor(0.3f, 0.0f, 0.3f, 1.0f)
        gl.clearDepth(1.0f)
        gl.clear(GL.COLOR_BUFFER_BIT or GL.DEPTH_BUFFER_BIT)
        gl.useProgram(texturedProgram.glProgram)




        gameObjects.forEach {
            if (!it.move(dt, t, keysPressed, gameObjects))
                gameObjects.remove(it)
        }
        gameObjects.forEach {
            it.update()
        }


        if (keysPressed.contains("SPACE") && Date().getTime().toFloat() - timeAtLastSpace > 300f) {
            timeAtLastSpace = Date().getTime().toFloat()
            follow = !follow
        }
        if (follow) {
            Camera.position.set(avatar.position)
            Camera.updateViewProjMatrix()
        } else {
            Camera.position.set(Vec2(0f, 0f))
            Camera.updateViewProjMatrix()
        }


        gameObjects.forEach {
            it.draw(Camera)
        }

    }
}
